import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store=new Vuex.Store(
  {
      state:{
        count:132,
        person:{
          name:""
        },
        eapCommend:[
          {
            name:"",
            level:"",
            speciality: ""
          },
          {
            name:"2",
            level:"",
            speciality: ""
          }
        ]
      },
      mutations:{
        setName(state,name){
          state.person.name=name;
        },
        add(state){
          state.count=state.count+1
        }
      }

  });
export default store;



