<template>
    <div id="information">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="line"></div>
      <div class="slide-box">
        <div class="nav" @click="tabToggle($event)">
          <mt-button size="small" @click.native.prevent="active = 'tab-container1'" class="active"></mt-button>
          <mt-button size="small" @click.native.prevent="active = 'tab-container2'"></mt-button>
          <mt-button size="small" @click.native.prevent="active = 'tab-container3'"></mt-button>
          <mt-button size="small" @click.native.prevent="active = 'tab-container4'"></mt-button>
          <mt-button size="small" @click.native.prevent="active = 'tab-container3'"></mt-button>
          <mt-button size="small" @click.native.prevent="active = 'tab-container4'"></mt-button>
        </div>
      </div>
      <div class="strong-line2"></div>
      <div class="page-tab-container">
        <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
          <mt-tab-container-item id="tab-container1">
            <!---->
            <div class="read shadow" @click="goTo">
              <div class="read-item1">
                <div class="test-info">
                  <div class="test-box">
                    <h3 class="test-title"></h3>
                    <p class="test-content"> </p>
                  </div>
                  <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
                </div>
              </div>
              <div class="read-item2">
                <p class="read-title"><br></p>
                <div class="read-img-box">
                  <div class="read-img"><img src="../../assets/img/home/p4.png" alt=""></div>
                  <div class="read-img"><img src="../../assets/img/home/p1.png" alt=""></div>
                  <div class="read-img"><img src="../../assets/img/home/p5.png" alt=""></div>
                </div>
              </div>
              <div class="read-item1">
                <div class="test-info">
                  <div class="test-box">
                    <h3 class="test-title"></h3>
                    <p class="test-content"> </p>
                  </div>
                  <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
                </div>
              </div>
              <div class="read-item2">
                <p class="read-title"><br></p>
                <div class="read-img-box">
                  <div class="read-img"><img src="../../assets/img/home/p4.png" alt=""></div>
                  <div class="read-img"><img src="../../assets/img/home/p1.png" alt=""></div>
                  <div class="read-img"><img src="../../assets/img/home/p5.png" alt=""></div>
                </div>
              </div>
              <div class="read-item1">
                <div class="test-info">
                  <div class="test-box">
                    <h3 class="test-title"></h3>
                    <p class="test-content"> </p>
                  </div>
                  <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
                </div>
              </div>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="tab-container2">
            <!-- cell -->
            <mt-cell v-for="n in 5" title="tab-container 2"></mt-cell>
          </mt-tab-container-item>
          <mt-tab-container-item id="tab-container3">
            <!-- cell -->
            <mt-cell v-for="n in 7" title="tab-container 3"></mt-cell>
          </mt-tab-container-item>
          <mt-tab-container-item id="tab-container4">
            <!-- cell -->
            <mt-cell v-for="n in 7" title="tab-container 4"></mt-cell>
          </mt-tab-container-item>
          <mt-tab-container-item id="tab-container5">
            <!-- cell -->
            <mt-cell v-for="n in 7" title="tab-container 5"></mt-cell>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>

    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue";
    export default {
      name: "index",
      data(){
        return{
          active:"tab-container1",
          showTitle:""
        }
      },
      components:{
        appHeader
      },
      methods:{
        backUp () {
          this.$refs.child.backUp();
        },
        tabToggle:function (e) {
          let el=e.target;
          let child=el.parentNode.children;
          for (let i=0;i<=4;i++){
            child[i].classList.remove('active');
          }
          el.classList.add('active');
        },
        goTo(e){
          this.$router.push('information/detail');
        }
      }
    }
</script>

<style scoped lang="less">
 #information {
   padding-bottom: 110px;
   .slide-box{
     overflow-x: scroll;
     .nav{
       padding-left: 26px;
       white-space: nowrap;
       width: 900px;
       button{
         padding: 0;
         &.active::before{
           left: 10px;
           right: 10px;
         }
       }
     }
   }
 }
 .test,.read{
   .test-info{
     padding-top: 28px;
     padding-bottom: 28px;
     overflow: hidden;
     .test-box{
       float: left;
       width: 496px;
       padding: 0 36px;
     }
     .test-title{
       font-size: 30px;
       line-height: 45px;
       color: #545454;
       font-weight: 400;
       margin-bottom: 12px;
     }
     .test-content{
       font-size:24px;
       height: 72px;
       color: #b1b1b1;
       line-height: 36px;
       font-weight:400;
       display: -webkit-box;
       -webkit-box-orient: vertical;
       -webkit-line-clamp: 2;
       overflow: hidden;
     }
     .test-pic{
       float: left;
       width:220px;
       height:160px;
     }
   }
 }
 .read{
   .read-item1,.read-item2{
     overflow: hidden;
     border-bottom: 1px solid #ededed;
     clear: both;
   }
   .read-item2{
     padding-bottom: 28px;
     .read-title{
       color: #545454;
       font-size: 30px;
       line-height: 45px;
       padding: 28px;
     }
     .read-img-box{
       .read-img{
         float: left;
         margin-right: 10px;
         width:220px;
         height:160px;
         border-radius:10px;
         &:first-child{
           margin-left: 36px;
         }
       }
     }
   }
 }
</style>



