<template>
    <div id="information-detail">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="info-box">
        <img src="../../assets/img/info/pic1.png" alt="">
        <p>
          <br>
          <br><br>

          100205300<br><br>

          <br><br>

          <br><br>

          <br><br>

          <br><br>

          

        </p>
      </div>
      <div class="tag-box">
        <span class="m-tag"></span>
        <span class="m-tag"></span>
      </div>
      <div class="btn-box">
        <span class="info-btn" @click="flag && thumbUp($event)">{{num}}</span><span class="info-btn"></span>
      </div>
      <div class="strong-line2"></div>
      <!---->
      <div class="read shadow">
        <div class="bg-box line"><h3 class="home-title"> <router-link class="more" to="/information"></router-link></h3></div>
        <div @click="goTo('/information/detail')" class="read-item1">
          <div class="test-info">
            <div class="test-box">
              <h3 class="test-title"></h3>
              <p class="test-content"> </p>
            </div>
            <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
          </div>
        </div>
        <div @click="goTo('/information/detail')" class="read-item2">
          <p class="read-title"><br></p>
          <div class="read-img-box">
            <div class="read-img"><img src="../../assets/img/home/p4.png" alt=""></div>
            <div class="read-img"><img src="../../assets/img/home/p1.png" alt=""></div>
            <div class="read-img"><img src="../../assets/img/home/p5.png" alt=""></div>
          </div>
        </div>
        <div @click="goTo('/information/detail')" class="read-item1">
          <div class="test-info">
            <div class="test-box">
              <h3 class="test-title"></h3>
              <p class="test-content"> </p>
            </div>
            <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
          </div>
        </div>
        <div @click="goTo('/information/detail')" class="read-item2">
          <p class="read-title"><br></p>
          <div class="read-img-box">
            <div class="read-img"><img src="../../assets/img/home/p4.png" alt=""></div>
            <div class="read-img"><img src="../../assets/img/home/p1.png" alt=""></div>
            <div class="read-img"><img src="../../assets/img/home/p5.png" alt=""></div>
          </div>
        </div>
        <div @click="goTo('/information/detail')" class="read-item1">
          <div class="test-info">
            <div class="test-box">
              <h3 class="test-title"></h3>
              <p class="test-content"> </p>
            </div>
            <img class="test-pic" src="../../assets/img/home/p2.png" alt="">
          </div>
        </div>
      </div>
      <div class="btn-box2">
        <button class="btn"></button>
        <button class="btn"></button>
        <button class="btn"></button>
      </div>
      <div class="strong-line2"></div>

      <div class="comments-box">
        <h3 class="comments-title">({{this.$store.state.count}})</h3>
        <div class="comments-item" v-for="(item,i) in txt">
          <div class="head-icon"><img src="../../assets/img/info/headicon.png" alt=""></div>
          <div class="comments-data">
            <h4></h4>
            <span>2019-2-18</span>
          </div>
          <div class="comments-content">
            {{item.length>maxLength?(item.substring(0, 67)+""):item}}
            <span v-if="item.length>maxLength" class="fold" @click="openTxt($event,i)"></span>
          </div>
        </div>
      </div>
      <div class="float-box">
        <textarea  class="float-area" ref="area" placeholder=""></textarea>
        <div @click="submit"><input class="sub-btn" type="button" value=""></div>
      </div>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue"

  export default {
    name: "detail",
    data(){
      return{
        showTitle:"",
        num:1234,
        maxLength:80,
        flag:true,
        txt:[
          ",",
          ",",
          ",,",
          ""
        ],
        dataId:[]
      }
    },
    components:{
      appHeader
    },
    created(){
      this.$store.commit("add");
    },
    methods: {
      backUp() {
        this.$refs.child.backUp();
      },
      thumbUp(e){
        let el=e.currentTarget;
        el.classList.add("active");
        this.num++;
        this.flag=false;
      },
      submit(){
        // console.log(this.$refs.area.value);
        this.$refs.area.value="";
      },
      openTxt(e,i){
        console.log(i);
        let el=e.target.parentNode;
        el.innerHTML=this.txt[i];
        el.classList.add("showAll");
      }
    }
  }
</script>

<style scoped lang="less">
  #information-detail{
    padding-bottom: 116px;
    .float-box{
      width: 100%;
      height: auto;
      background: #fff;
      position: fixed;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      z-index: 9;
      text-align: center;
      .float-area{
        text-align: left;
        margin-bottom: 28px;
        width:678px;
        height:60px;
        border:2px solid rgba(191,191,191,1);
        border-radius:30px;
        font-size: 24px;
        line-height: 60px;
        padding-left: 74px;
        background: rgba(255,255,255,1) url("../../assets/img/info/edit.png") no-repeat 30px center;
        background-size: 22px 22px;
        outline: none;
        &:focus {
          height: 300px;
          background-position: 22px 20px;
        }
        &:focus+div .sub-btn{
          opacity: 1;
          margin-right: 36px;
          margin-bottom: 28px;
          width: 100px;
          height: 40px;
        }
      }
      .sub-btn{
        transition: all .01s;
        float: right;
        background: #ededed;
        outline: none;
        border: 0;
        border-radius: 5px;
        opacity: 0;
        width: 0;
        height: 0;
        margin: 0;
      }
    }
    .info-box{
      padding: 36px;
      img{
        width: 100%;
      }
      p{
        font-size:30px;
        text-align: justify;
        line-height: 40px;
      }
    }
    .btn-box{
      width:440px;
      margin: 0 auto;
      padding-bottom: 36px;
      overflow: auto;
      .info-btn{
        width:160px;
        height:64px;
        border:2px solid rgba(191,191,191,1);
        border-radius:10px;
        float: left;
        background: url("../../assets/img/info/thumbUp1.png") no-repeat 30px center;
        background-size: 30px 32px;
        padding-left: 70px;
        line-height: 64px;
        font-size: 30px;
        &:last-child{
          float: right;
          background-image: url("../../assets/img/info/share.png");
        }
        &.active{
          background-image: url("../../assets/img/info/thumbUp2.png");
          border: 2px solid #4facfe;
          color: #4facfe;
          position: relative;
          &::after{
            content: "+1";
            display: block;
            width: 30px;
            height: 30px;
            text-align: center;
            position: absolute;
            top: 0;
            right: 30px;
            margin: auto;
            animation: slideInUp 1s;
            opacity: 0;
          }
        }
      }
    }
    .comments-box{
      .comments-title{
        font-size: 30px;
        padding: 36px 36px 0;
      }
      .comments-item{
        padding-top: 28px;
        border-bottom: 1px solid #ededed;
        padding-bottom: 28px;
      }
      .head-icon{
        margin-left: 36px;
        margin-right: 30px;
        float: left;
        width:80px;
        height:80px;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border-radius:50%;
        margin-bottom: 16px;
        img{
          max-width: 100%;
        }

      }
      .comments-data{
        color: #B1B1B1;
        float: left;
        h4{
          margin-top: 5px;
          margin-bottom: 20px;
        }
      }
      .comments-content{
        clear: both;
        width:658px;
        height:108px;
        line-height: 36px;
        text-align: justify;
        font-size:24px;
        color:rgba(191,191,191,1);
        margin: 0 auto;
        position: relative;
        &.showAll{
          height: auto;
        }
      }
      .fold{
        display: block;
        position: absolute;
        bottom: 0;
        right: 0;
        color: #4facfe;
      }
    }
  }
  @-webkit-keyframes slideInUp {
    from{
      opacity: 1;
      top: 0px;
    }
    to{
      top: -60px;
      opacity: 0;
    }
  }
  .tag-box{
    padding: 0 36px 30px;
  }

  .home-title {
    line-height: 34px;
    font-size: 36px;
    color: #545454;
    padding: 28px 36px;
    font-weight: 400;
    .more {
      float: right;
      font-size: 36px;
    }
  }
  .test, .read {
    .test-info {
      padding-top: 28px;
      padding-bottom: 28px;
      overflow: hidden;

      .test-box {
        float: left;
        width: 496px;
        padding: 0 36px;
      }

      .test-title {
        font-size: 30px;
        line-height: 45px;
        color: #545454;
        font-weight: 400;
        margin-bottom: 12px;
      }

      .test-content {
        font-size: 24px;
        height: 72px;
        color: #b1b1b1;
        line-height: 36px;
        font-weight: 400;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }

      .test-pic {
        float: left;
        width: 220px;
        height: 160px;
      }
    }
  }
  .read {
    .read-item1, .read-item2 {
      overflow: hidden;
      border-bottom: 1px solid #ededed;
      clear: both;
    }

    .read-item2 {
      padding-bottom: 28px;

      .read-title {
        color: #545454;
        font-size: 30px;
        line-height: 45px;
        padding: 28px;
      }

      .read-img-box {
        .read-img {
          float: left;
          margin-right: 10px;
          width: 220px;
          height: 160px;
          border-radius: 10px;

          &:first-child {
            margin-left: 36px;
          }
        }
      }
    }
  }

  .btn-box2{
    padding: 28px 36px;
    .btn{
      width:180px;
      height:60px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      box-shadow:0px 3px 5px 0px rgba(0, 0, 0, 0.35);
      border-radius:10px;
      margin-right: 54px;
      color: #fff;
      font-size: 30px;
      &:last-child{
        margin-right: 0;
      }
    }
  }
</style>



