      </div>
      <p class="question-txt">2</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[1]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[1]}}</div>
        </mt-range>
      </div>
      <div class="line"></div>
      <p class="question-title-txt"></p>
      <p class="question-txt">1</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[2]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[2]}}</div>
        </mt-range>
      </div>
      <p class="question-txt">2</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[3]"
