          <div slot="end" class="goal">{{rangeValue[3]}}</div>
        </mt-range>
      </div>
      <p class="question-txt">3
        </p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[4]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[4]}}</div>
        </mt-range>
      </div>
      <h3 class="question-title-txt">4</h3>
      <div class="area-box">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="question-title-txt">5</h3>
      <div class="area-box">
        <textarea class="area-input" placeholder=""></textarea>
