  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record3",
    data(){
      return{
        showTitle:'',
        rangeValue:[8,8,8,8,8],
      }
    },
    created(){
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      },
    }
  }
</script>

<style scoped lang="less">

  .tip-box{
    border-bottom: 1px solid #ededed;
  }

  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }
  .question-title-txt{
    font-size:24px;
    font-weight:400;
    color:rgba(84,84,84,1);
    line-height:36px;
    padding: 28px 36px;
    text-align: justify;

  }
  .question-txt{
    padding: 0 36px;
    text-align: justify;
    font-size:24px;
    font-weight:400;
    color:rgba(84,84,84,1);
    line-height:36px;
  }
  .range-box{
    padding: 0 36px;
    width: 600px;
    /*margin: 0 auto;*/
    margin-top: 10px;
    margin-bottom: 28px;
  }
  .goal{
    font-size: 30px;
    color: #4facfe;
    padding-left: 10px;
  }

  .btn-box{
    text-align: center;
    padding: 30px;
  }
</style>




