<template>
  <div id="addArchives">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="line"></div>
    <div class="tip-box">
      <h3 class=""></h3>
      <p>,</p>
    </div>
    <div class="e-field">
      <div class="field-item">
        <label for="role"></label>
        <select name="" id="role">
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <mt-field label="" placeholder="" v-model="username"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="jobnumber"></mt-field>
      <mt-field label="" placeholder="" v-model="department"></mt-field>
      <div class="field-item">
        <label></label>
        <label for="male">
          <input type="radio" name="sex" value="male" id="male">
          <span></span>
        </label>
        <label for="female">
          <input type="radio" name="sex" value="female" id="female">
          <span></span>
        </label>
      </div>
      <div class="field-item">
        <label for="year"></label>
        <select name="" id="year">
          <option></option>
          <option v-for="item in year">{{item}}</option>
          <!--<option>1990</option>-->
          <!--<option>1991</option>-->
          <!--<option>1992</option>-->
          <!--<option>1993</option>-->
          <!--<option>1994</option>-->
          <!--<option>1995</option>-->
          <!--<option>1996</option>-->
          <!--<option>1997</option>-->
          <!--<option>1998</option>-->
          <!--<option>1999</option>-->
        </select>
      </div>
      <div class="field-item">
        <label for="edu"></label>
        <select name="" id="edu">
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <div class="field-item">
        <label for="marry"></label>
        <select name="" id="marry">
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <mt-field label="" placeholder="" type="tel" v-model="phone1"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="phone2"></mt-field>
      <div class="field-item">
        <label for="city"></label>
        <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
        <div class="pop-box" v-show="popupVisible">
          <button @click="hideArea" class="close-btn"></button>
          <eap-area v-on:childEvent="changeCity"></eap-area>
        </div>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="tip-box">
      <h3 class=""></h3>
      <p>
        , <br>
        ,; <br>
        ; <br>
        ,.
      </p>
    </div>
    <div class="e-field">
      <mt-field label="" placeholder="" v-model="linkman"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="linkphone"></mt-field>
      <div class="field-item">
        <label for="relac"></label>
        <select name="" id="relac">
          <option></option>
          <option></option>
          <option></option>
          <option>/</option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/consult/selectArchives"><div class="submit-btn"></div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader.vue";
  import eapArea from "@/components/public/area.vue";

  export default {
    name: "addArchives",
    data(){
      return{
        showTitle:"",
        year:[],
        username:"",
        phone1:"",
        phone2:"",
        department:"",
        jobnumber:"",
        linkman:"",
        linkphone:"",
        birthday:'',
        selectData: {
          defaultIndex: 0, //
          selectStatus: false, // selectStatus/
        },
        popupVisible:false,
        btnTxt:""
      }
    },
    components:{
      appHeader,
      eapArea
      // oSelect,
      //
    },
    created:function(){
      let i=1930;
      while (i<=2019){
        this.year.push(i);
        i++;
      }
      console.log(this.year);
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
      showArea(){
        this.popupVisible=true;
      },
      hideArea(){
        this.popupVisible=false;
      },
      changeCity(data){
        console.log(data);
        this.btnTxt=data;
      }
    }
  }
</script>

<style scoped lang="less">
  .tip-box{
    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 20px;
    h3{
      font-size: 30px;
      line-height: 80px;
      font-weight: 400;
    }
    p{
      font-size: 24px;
      color: #b1b1b1;
      line-height: 36px;
    }
  }
.field-item{
  height: 84px;
  width: 714px;
  margin-left: 50px;
  font-size: 30px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
  padding-top: 10px;
  label{
    float: left;
    width: 220px;
    font-size: 30px;
  }
  input[type='radio']{
    width: 30px;
    height: 30px;
    overflow: visible;
    background: url("../../assets/img/consult/radiobBg2.png") no-repeat center;
    background-size: 30px 30px;
    vertical-align: sub;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
  }

  input:checked{
    position: relative;
    background: #fff;
  }

  input:checked::after {
    position: absolute;
    background: url("../../assets/img/consult/radioicon.png") no-repeat center;
    background-size: 30px 30px;
    content: "";
    width: 30px;
    height: 30px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}
  .btn-box{
    text-align: center;
    .submit-btn{
      width:678px;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      font-size: 36px;
      color: #fff;
      margin: 36px auto;
      line-height: 100px;
    }
  }
  .show-btn{
    appearance: none; /*  */
    -moz-appearance: none; /* Firefox */
    -webkit-appearance: none; /* Safari and Chrome */
    background: url("../../assets/img/consult/downArrow.png") no-repeat scroll 95% center transparent ;/* */
    background-size: 18px 10px;
    /*  */
    width:430px;
    height:60px;
    line-height:28px;
    border:0;
    outline: none;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    padding:0 20px 0 10px;
    text-align: left;
    color: #545454;
  }
  .pop-box{
    animation: slideInUp .5s;
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    z-index: 9;
    left: 0;
    bottom: 0;
    .close-btn{
      background: #fff;
      width: 100%;
      height: 60px;
      position: absolute;
      font-size: 30px;
      left: 0;
      bottom: 353px;
      color: #4facfe;
      text-align: right;
      padding-right: 36px;
      border-radius: 10px 10px 0px 0px;
    }
  }
  @-webkit-keyframes slideInUp {
    from{
      bottom:-100%;
    }
    to{
      bottom:0;
    }
  }
</style>





