<template>
  <div id="orderDetail">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="line"></div>
    <div class="eap-data">
      <h3></h3>
      <div class="eap-pic">
        <img src="" alt="">
      </div>
      <div class="eap-state">
        <p class="eap-name"></p>
        <p class="eap-way"></p>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="person-data">
      <h3></h3>
      <div class="data-box">
        <p></p>
        <p>12123123123</p>
        <p></p>
        <p></p>
        <p></p>
        <p>30</p>
        <p>{{dateFormat(orderTime)}}</p>
        <p></p>
        <p></p>
        <p>13422818291</p>
        <!--<button class="time-btn" @click="openPicker"></button>-->
      </div>
      <!--<mt-datetime-picker-->
        <!--id="time"-->
        <!--ref="picker"-->
        <!--v-model="pickerValue"-->
        <!--type="datetime"-->
        <!--:start-date="startTime"-->
        <!--@confirm="handleConfirm">-->
      <!--</mt-datetime-picker>-->
    </div>
    <div class="strong-line2"></div>
    <div class="order-data">
      <h3></h3>
      <div class="data-box bb1">
        <p>2019-03-20 10:10:34</p>
        <p>1234567890</p>
        <p>2019-03-2114:00-14:50</p>
        <div v-show="showUpdate=='cancel'">
          <p>2019-03-2114:00</p>
          <p></p>
        </div>
      </div>
      <div class="tips-txt">
         1. 1212
        2. 66
        3. 4006191990
      </div>
      <div class="data-box">
        <p> <router-link class="book" to="/notification"></router-link></p>
      </div>
    </div>
      <div class="btn-box" v-show="showUpdate=='showUpdate'">
        <router-link to="/consult/orderList" class="noway"></router-link>
        <router-link to="/consult/orderUpdate" class="ok"></router-link>
      </div>
  </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue";

  export default {
    name: "orderDetail",
    data() {
      return {
        showTitle: "",
        pickerValue:"",
        startTime:new Date(),
        orderTime:new Date(),
        showUpdate:this.$route.params.flag
      }
    },
    components: {
      appHeader
    },
    methods:{
      openPicker() {
        this.$refs.picker.open();
      },
      backUp () {
        this.$refs.child.backUp();
      },
      handleConfirm(data){
        this.orderTime = data;
      },
      //yyyy-MM-dd hh:mm:ss 
      dateFormat:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 00
         * 100   09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
      },
    }
  }
</script>

<style scoped lang="less">
  #orderDetail{
    padding-bottom: 120px;
  }
  .btn-box{
    position: fixed;
    width: 100%;
    height: 100px;
    bottom: 0;
    left: 0;
    z-index: 9;
    margin: 0 auto;
    text-align: center;
    display: flex;
    .ok,.noway{
      flex: 1;
      line-height: 100px;
      margin: 0;
      padding: 0;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      font-size:36px;
      color:rgba(255,255,255,1);
    }
    .noway{
      background: #EDEDED;
      color: #545454;
    }
  }
  .eap-data{
    padding: 28px 36px;
    overflow: auto;
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
    }
    .eap-pic{
      width:80px;
      height:80px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      margin-right: 40px;
      float: left;
    }
    .eap-state{
      float: left;
      .eap-name{
        font-size: 30px;
        margin-bottom: 18px;
      }
    }
  }
  .person-data,.order-data{
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
      padding: 0 36px;
      border-bottom: 1px solid #ededed;
    }
    .data-box{
      position: relative;
      padding: 14px 36px 28px;
      p{
        line-height: 50px;
      }
      .time-btn{
        font-size:24px;
        color:#4facfe;
        position: absolute;
        top: 46%;
        right: 36px;
      }
    }
  }
  .order-data{
    .data-box{
      padding-bottom: 14px;
      &.bb1{
        border-bottom: 1px solid #ededed;
      }
    }
    .book{
      color:#4facfe;
    }
  }
</style>



